/* Enhanced Professional CSS Reset and Variables */
:root {
  /* Premium Color Palette */
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --primary-light: #8b5cf6;
  --secondary: #06b6d4;
  --accent: #f59e0b;
  --success: #10b981;
  --warning: #f59e0b;
  --error: #ef4444;
  --premium: #8b5cf6;
  
  /* Enhanced Text Colors */
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --text-tertiary: #94a3b8;
  --text-white: #ffffff;
  --text-on-dark: #e2e8f0;
  
  /* Premium Background Colors */
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --bg-dark: #020617;
  --bg-darker: #000000;
  --bg-gradient: linear-gradient(135deg, #0f172a 0%, #1e1b4b 50%, #312e81 100%);
  --bg-gradient-alt: linear-gradient(135deg, #1e1b4b 0%, #312e81 50%, #4338ca 100%);
  --bg-glass: rgba(255, 255, 255, 0.08);
  --bg-glass-dark: rgba(255, 255, 255, 0.05);
  --bg-card: rgba(30, 41, 59, 0.6);
  --bg-card-dark: rgba(15, 23, 42, 0.8);
  
  /* Enhanced Shadows */
  --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.4), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.5), 0 4px 6px -2px rgba(0, 0, 0, 0.3);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.6), 0 10px 10px -5px rgba(0, 0, 0, 0.4);
  --shadow-glow: 0 0 30px rgba(99, 102, 241, 0.2);
  --shadow-glow-accent: 0 0 40px rgba(139, 92, 246, 0.3);
  
  /* Enhanced Border Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 20px;
  --radius-2xl: 24px;
  --radius-full: 9999px;
  
  /* Smooth Transitions */
  --transition-fast: 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', system-ui, sans-serif;
  line-height: 1.6;
  color: var(--text-primary);
  background: var(--bg-gradient);
  min-height: 100vh;
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  position: relative;
}

/* FIXED: Enhanced Background Animation - Remove backdrop-filter conflicts */
.background-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -3; /* Lower z-index to ensure it stays behind */
  overflow: hidden;
  perspective: 1000px;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}

.particle {
  position: absolute;
  background: radial-gradient(circle, var(--primary-light) 0%, transparent 70%);
  border-radius: 50%;
  animation: float3d 20s infinite ease-in-out;
  transform-style: preserve-3d;
}

.particle:nth-child(1) { 
  width: 120px; height: 120px; top: 20%; left: 10%; 
  animation-delay: 0s; background: radial-gradient(circle, var(--primary) 0%, transparent 70%);
}
.particle:nth-child(2) { 
  width: 80px; height: 80px; top: 60%; left: 80%; 
  animation-delay: -5s; background: radial-gradient(circle, var(--secondary) 0%, transparent 70%);
}
.particle:nth-child(3) { 
  width: 150px; height: 150px; top: 80%; left: 20%; 
  animation-delay: -10s; background: radial-gradient(circle, var(--accent) 0%, transparent 70%);
}
.particle:nth-child(4) { 
  width: 100px; height: 100px; top: 40%; left: 70%; 
  animation-delay: -15s; background: radial-gradient(circle, var(--primary-light) 0%, transparent 70%);
}
.particle:nth-child(5) { 
  width: 200px; height: 200px; top: 10%; left: 50%; 
  animation-delay: -7s; background: radial-gradient(circle, var(--secondary) 0%, transparent 70%);
}

@keyframes float3d {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg) scale(1);
    opacity: 0.1;
  }
  25% {
    transform: translate3d(50px, -30px, 20px) rotate(90deg) scale(1.1);
    opacity: 0.3;
  }
  50% {
    transform: translate3d(-30px, 40px, -10px) rotate(180deg) scale(0.9);
    opacity: 0.2;
  }
  75% {
    transform: translate3d(20px, -20px, 15px) rotate(270deg) scale(1.05);
    opacity: 0.25;
  }
}

/* FIXED: New 3D Geometric Shapes - Remove backdrop-filter */
.geometric-shapes {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  z-index: -2; /* Between background and content */
}

.shape {
  position: absolute;
  background: linear-gradient(135deg, var(--primary) 0%, transparent 50%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: shapeFloat 25s infinite linear;
  transform-style: preserve-3d;
  /* REMOVED: backdrop-filter: blur(10px); */
}

.shape-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  left: 5%;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  background: linear-gradient(135deg, var(--primary) 0%, transparent 70%);
  animation-delay: 0s;
  filter: blur(20px);
  opacity: 0.1;
}

.shape-2 {
  width: 400px;
  height: 400px;
  top: 60%;
  right: -100px;
  border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  background: linear-gradient(135deg, var(--secondary) 0%, transparent 70%);
  animation-delay: -10s;
  filter: blur(25px);
  opacity: 0.08;
}

.shape-3 {
  width: 250px;
  height: 250px;
  bottom: 20%;
  left: 60%;
  border-radius: 40% 60% 70% 30% / 40% 50% 60% 50%;
  background: linear-gradient(135deg, var(--accent) 0%, transparent 70%);
  animation-delay: -15s;
  filter: blur(15px);
  opacity: 0.12;
}

@keyframes shapeFloat {
  0%, 100% {
    transform: translate3d(0, 0, 0) rotate(0deg) scale(1);
  }
  33% {
    transform: translate3d(30px, -40px, 10px) rotate(120deg) scale(1.1);
  }
  66% {
    transform: translate3d(-20px, 30px, -5px) rotate(240deg) scale(0.95);
  }
}

/* FIXED: Enhanced Grid Background */
.grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(rgba(99, 102, 241, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(99, 102, 241, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  background-position: center center;
  animation: gridMove 40s linear infinite;
  transform-style: preserve-3d;
  perspective: 500px;
  z-index: -1;
}

@keyframes gridMove {
  0% {
    transform: translate3d(0, 0, 0) perspective(500px) rotateX(60deg);
  }
  100% {
    transform: translate3d(-50px, -50px, 0) perspective(500px) rotateX(60deg);
  }
}

/* FIXED: Enhanced Hero Section - Remove conflicting backdrop-filters */
.hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  background: 
    radial-gradient(ellipse at top left, rgba(99, 102, 241, 0.15) 0%, transparent 50%),
    radial-gradient(ellipse at bottom right, rgba(6, 182, 212, 0.1) 0%, transparent 50%),
    var(--bg-gradient);
  z-index: 1; /* Ensure content is above background */
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transform-style: preserve-3d;
}

.floating-elements {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}

.floating-element {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, var(--primary-light) 0%, transparent 70%);
  filter: blur(40px);
  opacity: 0.1;
  animation: floatElement3d 15s infinite ease-in-out;
  transform-style: preserve-3d;
}

.floating-element.el-1 {
  width: 400px;
  height: 400px;
  top: 10%;
  left: 10%;
  background: radial-gradient(circle, var(--primary) 0%, transparent 70%);
  animation-delay: 0s;
  transform: translate3d(0, 0, 50px);
}

.floating-element.el-2 {
  width: 300px;
  height: 300px;
  top: 60%;
  right: 10%;
  background: radial-gradient(circle, var(--secondary) 0%, transparent 70%);
  animation-delay: -5s;
  transform: translate3d(0, 0, -30px);
}

.floating-element.el-3 {
  width: 250px;
  height: 250px;
  bottom: 20%;
  left: 20%;
  background: radial-gradient(circle, var(--accent) 0%, transparent 70%);
  animation-delay: -10s;
  transform: translate3d(0, 0, 20px);
}

.floating-element.el-4 {
  width: 350px;
  height: 350px;
  top: 30%;
  right: 20%;
  background: radial-gradient(circle, var(--primary-light) 0%, transparent 70%);
  animation-delay: -7s;
  transform: translate3d(0, 0, -50px);
}

@keyframes floatElement3d {
  0%, 100% {
    transform: translate3d(0, 0, 0) scale(1) rotate(0deg);
  }
  25% {
    transform: translate3d(40px, -50px, 20px) scale(1.1) rotate(90deg);
  }
  50% {
    transform: translate3d(-60px, 30px, -10px) scale(0.9) rotate(180deg);
  }
  75% {
    transform: translate3d(20px, -40px, 30px) scale(1.05) rotate(270deg);
  }
}

/* FIXED: Enhanced Sections with proper z-index hierarchy */
.skills-section,
.experience-section,
.projects-section {
  position: relative;
  overflow: hidden;
  z-index: 1; /* Ensure content stays above background */
}

.skills-section::before,
.experience-section::before,
.projects-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(ellipse at 20% 80%, rgba(99, 102, 241, 0.05) 0%, transparent 50%),
    radial-gradient(ellipse at 80% 20%, rgba(6, 182, 212, 0.05) 0%, transparent 50%);
  z-index: -1;
  pointer-events: none;
}

/* Enhanced Skills Section Background */
.skills-section {
  background: 
    linear-gradient(135deg, rgba(15, 23, 42, 0.9) 0%, rgba(30, 27, 75, 0.8) 50%, rgba(49, 46, 129, 0.9) 100%),
    var(--bg-gradient);
}

/* Enhanced Experience Section Background */
.experience-section {
  background: 
    linear-gradient(135deg, rgba(30, 41, 59, 0.95) 0%, rgba(15, 23, 42, 0.9) 50%, rgba(30, 27, 75, 0.85) 100%);
}

/* Enhanced Projects Section Background */
.projects-section {
  background: 
    linear-gradient(135deg, rgba(15, 23, 42, 0.95) 0%, rgba(30, 27, 75, 0.85) 50%, rgba(49, 46, 129, 0.9) 100%);
}

/* FIXED: Enhanced Container with proper z-index */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 2; /* Higher than section background but lower than content */
}

/* FIXED: Enhanced Cards - Remove backdrop-filter or use alternative */
.skill-card,
.timeline-content,
.project-card {
  background: var(--bg-card);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-2xl);
  padding: 2.5rem;
  transition: var(--transition-normal);
  box-shadow: 
    var(--shadow-lg),
    inset 0 1px 0 rgba(255, 255, 255, 0.1),
    0 8px 32px rgba(0, 0, 0, 0.3);
  /* REMOVED: backdrop-filter: blur(20px); */
  position: relative;
  overflow: hidden;
}

/* Alternative glass effect without backdrop-filter */
.skill-card::after,
.timeline-content::after,
.project-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0.05) 100%
  );
  border-radius: var(--radius-2xl);
  pointer-events: none;
  z-index: -1;
}

.skill-card::before,
.timeline-content::before,
.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.3) 50%, 
    transparent 100%);
}

.skill-card:hover,
.timeline-content:hover,
.project-card:hover {
  transform: translateY(-8px) translateZ(10px);
  box-shadow: 
    var(--shadow-xl), 
    var(--shadow-glow),
    0 12px 40px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.2);
}

/* FIXED: Premium Navigation - Reduce backdrop-filter intensity */
.navbar {
  background: rgba(15, 23, 42, 0.85); /* More opaque background */
  /* REDUCED: backdrop-filter: blur(10px) saturate(180%); */
  backdrop-filter: blur(5px) saturate(150%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding: 1rem 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: var(--transition-normal);
}

.navbar.scrolled {
  background: rgba(15, 23, 42, 0.95);
  box-shadow: var(--shadow-xl);
  border-bottom-color: rgba(255, 255, 255, 0.15);
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-white);
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: var(--transition-fast);
}

.logo:hover {
  color: var(--primary-light);
  transform: translateY(-1px);
}

.logo-icon {
  font-size: 1.25rem;
}

.nav-buttons {
  display: flex;
  gap: 0.5rem;
  background: var(--bg-glass);
  padding: 0.5rem;
  border-radius: var(--radius-xl);
  border: 1px solid rgba(255, 255, 255, 0.1);
  /* REMOVED: backdrop-filter: blur(10px); */
}

/* FIXED: Modern Carousel - Remove backdrop-filter */
.modern-carousel {
  background: var(--bg-card);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-2xl);
  padding: 3rem;
  box-shadow: var(--shadow-lg);
  margin-top: 2rem;
  /* REMOVED: backdrop-filter: blur(20px); */
  animation: fadeInUp 0.6s ease-out;
}

/* FIXED: CTA Card - Remove backdrop-filter */
.cta-card {
  background: var(--bg-card);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-2xl);
  padding: 4rem;
  text-align: center;
  box-shadow: var(--shadow-xl), var(--shadow-glow);
  /* REMOVED: backdrop-filter: blur(20px); */
}

/* FIXED: Contact items - Remove backdrop-filter */
.contact-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: var(--text-on-dark);
  font-weight: 500;
  padding: 1rem 1.5rem;
  background: var(--bg-glass);
  border-radius: var(--radius-xl);
  border: 1px solid rgba(255, 255, 255, 0.15);
  transition: var(--transition-fast);
  font-size: 0.9rem;
  text-decoration: none;
  /* REMOVED: backdrop-filter: blur(10px); */
}

/* FIXED: Hero badge - Remove backdrop-filter */
.hero-badge {
  display: inline-block;
  background: var(--bg-glass);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--primary-light);
  padding: 0.5rem 1.5rem;
  border-radius: var(--radius-full);
  font-weight: 600;
  margin-bottom: 2rem;
  /* REMOVED: backdrop-filter: blur(10px); */
  animation: fadeInUp 0.8s ease-out 0.5s both;
}

/* FIXED: Timeline marker - Remove backdrop-filter */
.timeline-marker {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--bg-card);
  border: 3px solid var(--primary);
  margin-right: 2rem;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  box-shadow: var(--shadow-glow);
  /* REMOVED: backdrop-filter: blur(10px); */
}

/* FIXED: Projects badge - Remove backdrop-filter */
.projects-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  color: var(--text-white);
  padding: 1rem 2rem;
  border-radius: var(--radius-full);
  font-weight: 600;
  margin: 2rem 0;
  box-shadow: var(--shadow-glow);
  font-size: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  /* REMOVED: backdrop-filter: blur(10px); */
}

